.icon {
    fill: $color-yellow;

    width: 12rem;
    height: auto;
    position: fixed;
    z-index: 999;

    opacity: 1;
    // transition: opacity 0.8s ease 0.2s;

    &--hidden {
        // opacity: 0;
        display: none;
    }

    svg {
        position: relative;
        left: -50%;
        @include respond(tab-land) {
            left: -30%;
        }
    }

    &--mouse-wheel {
        display: block;
        animation: wheel 1.2s cubic-bezier(0.15, 0, 0.51, 1) 0s infinite normal
            none;

        @include respond(tab-land) {
            display: none;
        }
    }

    &--mouse {
        display: block;
        @include respond(tab-land) {
            display: none;
        }
    }

    &--touch {
        display: none;
        @include respond(tab-land) {
            display: block;
        }
    }

    &__chev-down {
        animation: down-fade 1.2s linear 0s infinite normal none;
    }

    &__chev-up {
        animation: up-fade 1.2s linear 0s infinite normal none;
    }

    &__chev-right {
        animation: right-fade 1.2s linear 0s infinite normal none;
    }

    &__chev-left {
        animation: left-fade 1.2s linear 0s infinite normal none;
    }
}

.close-icon {
    position: relative;
    width: 100%;
    height: 100%;
    display: block;
    cursor: pointer;

    transition: all 0.2s;

    transform-origin: 75% 50%;

    &::before,
    &::after {
        position: absolute;
        top: 1rem;
        left: 0;
        display: inline-block;
        width: 150%;
        height: 4px;
        border-radius: 2px;
        background-color: $color-white;

        content: "";
    }

    &::before {
        transform: rotate(-135deg);
    }

    &::after {
        transform: rotate(135deg);
    }

    &:hover {
        transform: scale(1.2);
    }
}
